
import "../assets/css/mint.css";
import { connectWallet, getCurrentWalletConnected } from "../util/interact.js";

import { useEffect, useState } from "react";
import { useHistory } from "react-router-dom";
import { BigNumber } from 'ethers'

import { nftAddress } from '../constants/address'
import { tokenAddress } from '../constants/address'
import { chainId } from '../constants/address'

import plusimg from "../assets/images/Plus-sign.png";
import minusimg from "../assets/images/minus-sign.png";


import Web3 from 'web3'


function MintPage() {
  const [walletAddress, setWallet] = useState("");
  const [status, setStatus] = useState("");
  let history = useHistory();

  const [mint, setMint] = useState(0);
  const [loading, setMintLoading] = useState(false);

  const rpcURL = "https://data-seed-prebsc-1-s1.binance.org:8545";
  const web3 = new Web3(rpcURL);


  useEffect(async () => {
    const { address, status } = await getCurrentWalletConnected();

    setWallet(address);
    setStatus(status);

    addWalletListener();
  }, []);


  function addWalletListener() {
    if (window.ethereum) {
      window.ethereum.on("accountsChanged", (accounts) => {
        if (accounts.length > 0) {
          setWallet(accounts[0]);

        } else {
          setWallet("");
          history.push("/")
        }
      });
    } else {
      setStatus(
        <p>
          <a
            target="_blank"
            href={`https://metamask.io/download.html`}
            rel="noreferrer"
          >
            You must install Metamask, a virtual Ethereum wallet, in your
            browser.
          </a>
        </p>
      );
    }
  }

  const onPlus = () => {
    if (mint <= 12000) {
      setMint(mint + 1);
    }
  }

  const onMinus = () => {
    if (mint >= 1) {
      setMint(mint - 1);
    }
  }

  const onMint = async () => {
    if (walletAddress.length > 0) {
      if (mint > 0 && mint <= 12000) {
        setMintLoading(true);
  
        let tokenContract, nftContract;
  
        
        const nftAbi = require("../constants/nftabi.json");
        window.contract = await new web3.eth.Contract(nftAbi, nftAddress);
        nftContract = window.contract;
  
        let price = await nftContract.methods.getNftPrice().call();
        price = String(Number(price) * mint);

        if (Number(price) > 0) {
  
          const tokenAbi = require("../constants/tokenabi.json");
          window.contract = await new web3.eth.Contract(tokenAbi, tokenAddress);
          tokenContract = window.contract;
  
          let balance = await tokenContract.methods.balanceOf(walletAddress).call();
  
          if (Number(balance) < Number(price)) {
            alert("It's not enough LOOKS for mint!");
            setMintLoading(false);
            setMint(0);
            return
          }
            
          const approveTransactionParameters = {
            to: tokenAddress,
            from: walletAddress,
            data: tokenContract.methods.approve(nftAddress, price).encodeABI(),
          };
          await window.ethereum.request({
            method: "eth_sendTransaction",
            params: [approveTransactionParameters],
          })
        }
  
        const mintTransactionParameters = {
          to: nftAddress,
          from: walletAddress,
          data: nftContract.methods.mintToken(mint).encodeABI(),
        };
        await window.ethereum.request({
          method: "eth_sendTransaction",
          params: [mintTransactionParameters],
        })
  
        setMintLoading(false);
        setMint(0);
      } else {
        alert("Please input amount of mint.")
      }
    }
  }

  const changeMintCount = (e) => {
    console.log(e.target.value);
  }


  let mintButton;
  if (loading) {
    mintButton = 
    <button 
        className="mintbtn"
        style={{
            width: '80%',
            fontSize: '30px',
            marginTop: '20px',
            padding: '9px 15px',
            color: 'blue',
            backgroundColor: '#92d050',
            textTransform: 'uppercase'}}> 
            Minting... 
    </button>
  } else {
      mintButton = 
      <button 
          className="mintbtn"
          onClick={onMint}
          style={{
              width: '80%',
              fontSize: '30px',
              marginTop: '20px',
              padding: '9px 15px',
              color: 'blue',
              backgroundColor: '#92d050',
              cursor: 'pointer',
              textTransform: 'uppercase'}}> 
              Mint 
      </button>
  }
  
  return (
    <>
        <div id="mint" style={{ height: '100%', minHeight: '100vh'}}>
            <div>
                <div className="w-col w-col-12">
                    <h1 className="svelte-1urqv0k">Looks DAO</h1>
                    <h1 className="svelte-1urqv1k" style={{ textAlign: 'center' }}>
                    Mint Your <span style={{ textTransform: 'capitalize' }}> </span> NFT Now!
                    </h1>
                </div>
            </div>
            <div>
                <div className="w-col w-col-12 mint-container">
                    <div className="minus">
                        <button
                            onClick={onMinus} 
                            style={{
                              backgroundColor: '#262d37',
                              borderRadius: '50%',
                              "&:hover" : {
                                  backgroundColor: 'white !important',
                              }
                            }}>
                            <img
                                src={minusimg} 
                                style={{width: '80px'}}/>
                        </button>
                    </div>
                    <div className="main-section" style={{ color: 'white' }}>
                        <div className="w-col w-col-12">
                            <input
                                className=""
                                onChange={changeMintCount}
                                value={mint}
                                type="number"
                                style={{
                                    marginTop: 0,
                                    fontSize: '34px',
                                    backgroundColor: 'white',
                                    padding: '5px 15px',
                                    width: '70%'
                                }} 
                            />
                            {mintButton}
                        </div>
                    </div>
                    <div className="plus">
                        <button 
                            onClick={onPlus}
                            style={{ 
                                borderRadius: '50%',
                                backgroundColor: '#262d37'
                                }}
                        >
                            <img src={plusimg} 
                                style={{width: '80px'}}/>
                        </button>
                    </div>
                    
                </div>
                <p style={{ textAlign: 'center', fontSize: '.875rem', color: '#b3b6bb'}}>
                    ¬© 2022 Equal Crypto People | All Rights Reserved. Copy or duplication is prohibited.
                </p>
                <p style={{ textAlign: 'center', fontSize: '.875rem', color: '#b3b6bb'}}>
                Site Developed by:&nbsp;
                    <a href="https://govcon.ndemandconsulting.com/" style={{ color: '#0bb1f8'}}>Looks DAO.</a>
                </p>
            </div>
            
        </div>
    </>
  );
}

export default MintPage;
