import './App.css';
import {
  BrowserRouter as Router,
  Switch,
  Route,
} from "react-router-dom";
import MainPage from './views/Home';
import Mint from './views/Mint';

function App() {
  return (
    <Router>
      <Switch>
        <Route exact path="/" component={MainPage} />
        <Route path="/mint" component={Mint} />
      </Switch>
    </Router>
  );
}

export default App;
